
.status-effects-trigger {
  display: flex;
  gap: 8px;
  align-items: flex-start;
  position: relative;
}

.status-button {
  background: transparent;
  border: none;
  font-family: var(--pixel-font);
  font-size: 10px;
  font-weight: bold;
  cursor: pointer;
  padding: 4px 8px;
  transition: color 0.2s;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 4px;
}

.status-button.active {
  color: #1976d2;
  text-decoration: underline;
}

.button-icon {
  font-size: 12px;
}

.status-effects-popover {
  position: absolute;
  bottom: calc(100% + 10px);
  left: 0;
  width: 320px;
  background: var(--bg-input);
  border: 3px solid var(--border-color);
  border-radius: 4px;
  padding: 0;
  box-shadow: 4px 4px 0 rgba(0, 0, 0, 0.3);
  z-index: 30;
  animation: popoverFadeIn 0.2s ease-out;
}

@keyframes popoverFadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.popover-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 11px;
  padding: 10px 12px;
  background: var(--bg-card);
  border-bottom: 2px solid var(--border-color);
  color: #000;
}

.popover-close {
  background: transparent;
  border: none;
  font-size: 16px;
  line-height: 1;
  cursor: pointer;
  color: #666;
  transition: color 0.2s;
  padding: 0 4px;

  &:hover {
    color: #000;
  }
}

.status-effects-content {
  display: flex;
  flex-direction: column;
  padding: 8px;
}

.status-tabs {
  display: flex;
  border: 2px solid var(--border-color);
  border-bottom: none;
  background: #fff;
  margin-bottom: 0;
  overflow: hidden;
}

.status-tab {
  flex: 1;
  padding: 8px 0;
  background: #fff;
  border: none;
  font-family: var(--pixel-font);
 	font-size: 10px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s, color 0.2s;
  color: #000;
  position: relative;
}

.status-tab + .status-tab::before {
  content: '';
  position: absolute;
  left: -1px;
  top: 0;
  bottom: 0;
  border-left: 2px solid var(--border-color);
}

.status-tab::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  border-bottom: 2px solid black;
}

.status-tab:hover {
  background: #f5f5f5;
}

.status-tab.active {
  color: #1976d2;
}

.status-list-content {
  max-height: 280px;
  overflow-y: auto;
  padding-right: 4px;
  border: 2px solid var(--border-color);
  border-top: none;
  padding: 12px;

  /* 自定义滚动条样式 */
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg-card);
    border-radius: 4px;
    border: 1px solid var(--border-color);
  }

  &::-webkit-scrollbar-thumb {
    background: #999;
    border-radius: 4px;
    border: 1px solid var(--border-color);

    &:hover {
      background: #777;
    }

    &:active {
      background: #555;
    }
  }

  /* Firefox 滚动条样式 */
  scrollbar-width: thin;
  scrollbar-color: #999 var(--bg-card);
}


@media (max-width: 768px) {
  .status-effects-trigger {
    display: none;
  }
}
