
.ability-status-panel {
  background: var(--bg-input);
  border: 2px solid var(--border-color);
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.ability-status-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.ability-status-title {
  font-weight: bold;
  font-size: 10px;
  color: #000;
}

.ability-status-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.ability-status-item {
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  padding: 6px;
  font-size: 8px;
  text-align: left;
  line-height: 1.3;
  cursor: pointer;
  user-select: none;
  touch-action: none;

  &.placeholder {
    border-style: dashed;
    color: #888;
    background: #fafafa;
    pointer-events: none;
  }
}

.ability-status-name {
  font-weight: bold;
  font-size: 9px;
  margin-bottom: 2px;
  color: #000;
}

.ability-status-desc {
  font-size: 8px;
  color: #111;
}

.ability-status-eval {
  font-size: 8px;
  color: #222;
  margin-top: 2px;
}

.ability-status-empty {
  font-size: 9px;
  color: #666;
  text-align: center;
}
