@import"https://fontsapi.zeoseven.com/570/main/result.css";:root{--bg-dark: #0a0a0a;--bg-darker: #050505;--text-primary: #ffffff;--text-secondary: #cccccc;--border-color: #000000;--bg-panel: #f5f5f5;--bg-input: #ffffff;--bg-card: #f0f0f0;--bg-card-hover: #e0e0e0;--bg-success: #90ee90;--bg-error: #ffb3b3;--bg-highlight: #ffffcc;--bg-warning: #ffe4b3;--pixel-font: 'Fusion Pixel 12px M latin', 'Courier New', monospace}@keyframes fadeIn{from{opacity:0;transform:translateY(10px)}to{opacity:1;transform:translateY(0)}}@keyframes pulse{0%,100%{opacity:.6}50%{opacity:1}}@keyframes bubblePop{from{opacity:0;transform:translateX(-50%) translateY(5px)}to{opacity:1;transform:translateX(-50%) translateY(0)}}*{margin:0;padding:0;box-sizing:border-box}.status-system-wrapper{width:100%;background:rgba(0,0,0,0);position:relative}.status-system{width:800px;aspect-ratio:16/9;background:var(--bg-panel);border:4px solid var(--border-color);position:relative;overflow:hidden;display:flex;flex-direction:column;font-family:var(--pixel-font);font-weight:normal;animation:fadeIn .5s ease-out}.status-system::before{content:"";position:absolute;inset:0;background-image:linear-gradient(90deg, rgba(0, 0, 0, 0.03) 1px, transparent 1px),linear-gradient(rgba(0, 0, 0, 0.03) 1px, transparent 1px);background-size:15px 15px;z-index:1;pointer-events:none}.main-content{flex:1;display:flex;position:relative;z-index:2;min-height:0}.character-panel{flex:2;padding:5px;display:flex;flex-direction:column;gap:10px;background:var(--bg-card);min-height:0}.mobile-section{display:flex;flex-direction:column;overflow:visible}.mobile-section__body{display:block;overflow:visible}.inventory-container .mobile-section__body{flex:none;min-height:auto;display:block}.right-panel{flex:1;display:flex;flex-direction:column;border-left:3px solid var(--border-color);min-height:0}.inventory-container{flex:none;min-height:auto}.collapse-toggle{width:100%;display:none;justify-content:space-between;align-items:center;padding:8px 12px;bac